'use client';
import { Row, Col, Button } from 'react-bootstrap';
import InputField from '../components/InputField';
import { FormControl, InputLabel, Select, MenuItem } from '@mui/material';

export default function UserForm({ formData, handleChange, status, setStatus }) {
  return (
    <form style={{ width: '100%', maxWidth: '600px' }}>
      <Row>
        <Col><InputField label="Name" name="name" value={formData.name} onChange={handleChange} /></Col>
        <Col><InputField label="Surname" name="surname" value={formData.surname} onChange={handleChange} /></Col>
        <Col xs="auto">
          <img
            src="/images/user.jpg"
            alt="Profile"
            style={{ width: 100, height: 100, borderRadius: '50%', marginTop: 30 }}
          />
        </Col>
      </Row>
      <Row className="mt-3">
        <Col><InputField label="Email" name="email" value={formData.email} onChange={handleChange} /></Col>
        <Col><InputField label="Phone" name="phone" value={formData.phone} onChange={handleChange} /></Col>
      </Row>
      <Row className="mt-3">
        <Col><InputField label="ID" name="id" value={formData.id} onChange={handleChange} /></Col>
        <Col><InputField label="Address" name="address" value={formData.address} onChange={handleChange} /></Col>
        <Col><InputField label="City" name="city" value={formData.city} onChange={handleChange} /></Col>
      </Row>
      <Row className="mt-3">
        <Col>
          <FormControl fullWidth>
            <InputLabel>Sub type</InputLabel>
            <Select
              name="subtype"
              value={formData.subtype}
              onChange={(e) => handleChange("subtype", e.target.value)}
              label="Sub type"
            >
              <MenuItem value="Standard">Standard</MenuItem>
              <MenuItem value="Premium">Premium</MenuItem>
              <MenuItem value="VIP">VIP</MenuItem>
            </Select>
          </FormControl>
        </Col>
        <Col>
          <FormControl fullWidth>
            <InputLabel>Status</InputLabel>
            <Select value={status} onChange={(e) => setStatus(e.target.value)} label="Status">
              <MenuItem value="user">user</MenuItem>
              <MenuItem value="admin">admin</MenuItem>
            </Select>
          </FormControl>
        </Col>
      </Row>
      <div className="d-flex justify-content-center mt-4">
        <Button variant="success" style={{ width: '150px', backgroundColor: '#33b5aa', border: 'none' }}>
          Create
        </Button>
      </div>
    </form>
  );
}
